import { useQuery } from '@apollo/react-hooks';
import gql from "graphql-tag";
import Course from "./Course"

import React, { Component } from 'react';
const CoursePage = () => {
 
  const { data, loading, error } = useQuery(Cdata)

  if (loading) return <p>Loading...</p>;
  if (error) return <p>Error</p>;

  const gradeN = [[1, 2], [3, 4], [5, 6], [7, 8], [9]];
  const courses =   data.courses  

  return (
    <section className="course-one course-page">
      <div className="container">
        <div className="row">

          <div className="col-lg-12">
            <div className="course-details__content">

              <div style={{ textAlign: "center" }}>

                <ul className="course-details_grade-tab course-details__tab-navs list-unstyled nav nav-tabs  " role="tablist">

                  {
                    gradeN.map((t, index) => {
                      return (<li>
                        <a className={index == Math.round((gradeN.length -1)/2) ? "active" : ""} key={index.toString()} role="tab" data-toggle="tab" href={'#G' + index}>{gradeN.length -1  == index ?'Grade - ' + t.toString() + ' +': 'Grade - ' + t.toString()  }</a>
                      </li>)
                    })
                  }

                </ul>
              </div>

              <div className="tab-content course-details__tab-content ">
                {
                  gradeN.map((t, index) => {

                    return (
                      <div key={index.toString()} className={index == Math.round((gradeN.length -1)/2) ? "show active tab-pane animated fadeInUp" : "tab-pane  animated fadeInUp"} role="tabpanel" id={'G' + index}>
                        <div className="row">

                          {
                            courses.map((elem) => {

                              if(
                                t.includes(parseInt(elem.Grade)) 
                                ||
                                (gradeN.length -1 == index  && parseInt(elem.Grade) >= gradeN[gradeN.length -1][0])
                              ) {
                                return (
                                  <Course course={elem} />
                                )
                              }
                            })
                          }


                        </div>
                      </div>
                    )
                  })
                }
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};
const Cdata = gql`
{
      courses{
      Name
      Description
      ImageUrl
      Type
      Grade
      Pdf
      DurationHrs
      Sessions
      Price
      Frequency
      PaymentLink
      projects{
          name
        description
        imageurl
      }
      course_category{
          EndAge
        StartAge
        Name

      }
      coupon{
          discount
        unit
        active
        expiresBy
      }
    }
  }`

export default CoursePage;