var player3 = new Array();
var playing3 = false;


class VideoTwo extends Component {
    constructor(props) {
        super(props)
        this.state = {
            isOpen: false,
            testimony: this.props.data
        }
    }
    
    openModal = () => {
        this.setState({ isOpen: true })
    }

    onYouTubeIframeAPIReady() {

        const { testimony } = this.state;
        testimony.map((t) => {
            player3[t.number] = new YT.Player('video3' + t.number, {
                events: {
                    'onStateChange': this.onPlayerStateChange,
                },
                // host: 'https://www.youtube.com',
            });

        })

    };

    onPlayerStateChange = (event) => {
        if (event.data == YT.PlayerState.PLAYING) {
            // console.log('video started');
            $('#carousel-example-2z').carousel('pause');
            playing3 = true;
            $(".controlsParents").hide()
        }

        else if (event.data == YT.PlayerState.PAUSED) {
            // console.log('video paused');
            $('#carousel-example-2z').carousel('cycle');
            playing3 = false;
            $(".controlsParents").show()

        }

        return 1;
    };

    componentDidMount = () =>{
        $('#carousel-example-2z').carousel('cycle');
        //this.onYouTubeIframeAPIReady()

    } 

    render() {
 
        const { testimony } = this.state

        return (
            <section className="video-two">
                <div className="container">
                    {/* <img src="/assets/images/scratch-1-1.png" className="video-two__scratch" alt="" /> */}
                    <div className="row">
                        <div className="col-lg-12">
                            <div className="video-two__content">
                                <h2 className="video-two__title">What people has to say about us</h2>
                                {/* <a href="#" className="thm-btn">Learn More</a> */}
                            </div>
                        </div> 
                        {/* <div className="col-lg-4 d-flex" style={{ justifyContent: "center" }}>
                            <div className="my-auto">
                                <ModalVideo channel='youtube' isOpen={this.state.isOpen} videoId='P05kfbspycs' onClose={() => this.setState({ isOpen: false })} />
                                <div onClick={this.openModal} className="video-two__popup"><i className="fa fa-play"></i>
                                </div>
                            </div>
                        </div> */}

                        <div className="col-lg-12 clearfix">
                            <div id="carousel-example-2z" className="carousel slide carousel-fade" data-ride="carousel" data-interval = "4600"  data-pause = "hover" >
                                <ol className="carousel-indicators">
                                    {
                                        testimony.map((t, index) => {
                                            return (
                                                <li key={index.toString()} data-target="#carousel-example-2z" data-slide-to={t.number} className={t.number == '1' ? 'active' : null}></li>

                                            )
                                        })
                                    }

                                </ol>
                                <div className="carousel-inner" role="listbox">

                                    {
                                        testimony.map((t, index) => {
                                            return (
                                                <div key={index.toString()} className={t.number == '1' ? 'carousel-item active' : 'carousel-item'} >
                                                    <div className="col-xl-5 carousel-youtube_video">
                                                        {/*<iframe  loading="lazy" id = {'video3' + t.number}  src={t.VideoUrl + '?enablejsapi=1'} frameBorder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowFullScreen=""></iframe>*/}
                                                        <iframe id={`video3` + t.number} data-src={t.VideoUrl} className="lazyload" frameBorder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope;" allowFullScreen></iframe>
                                                    </div>
                                                </div>
                                            )
                                        })
                                    }

                                </div>
                                <div className = "controlsParents">
                                    <a className="carousel-control-prev" href="#carousel-example-2z" role="button" data-slide="prev">
                                        <span className="carousel-control-prev-icon" aria-hidden="true"></span>
                                        <span className="sr-only">Previous</span>
                                    </a>
                                    <a className="carousel-control-next" href="#carousel-example-2z" role="button" data-slide="next">
                                        <span className="carousel-control-next-icon" aria-hidden="true"></span>
                                        <span className="sr-only">Next</span>
                                    </a>
                                </div>
                            </div>

                        </div>

                    </div>
                </div>
            </section>
        );
    }
}

export default VideoTwo;
