import React, { Component } from 'react';
import Countdown from 'react-countdown';
import { useQuery } from '@apollo/react-hooks';
import { gql, useMutation } from '@apollo/client';
// import { Mutation } from 'react-apollo'

// class CountdownKipso extends Component {
    const CountdownKipso = (props) => {
       const handleButtonClick = () =>{
        document.getElementById("userform").reset();
        var x = document.getElementById("snackbar");
        x.className = "show";
        setTimeout(function(){ x.className = x.className.replace("show", ""); }, 3000);
        }
       
        // constructor(props){
        //     super(props)
        //     this.state = {
        //         name: '',
        //         email: '',
        //         phone_no: '',
        //         comment: '',
        //     }
        // }
        // render(){
        const [ createUserForm] = useMutation(ADD_DATA);

        return (
            
            <section className="countdown-one">
                <div className="container">
                    <div className="row">
                        <div className="col-lg-6">
                            <div className="countdown-one__content">
                                <h2 className="countdown-one__title">Register now </h2>
                                <p className="countdown-one__tag-line">Why wait ! Start learning now !</p>
                                <p className="countdown-one__text">“In the 21st century, scientific and technological innovations have become increasingly important as we face the benefits and challenges of both globalization and a knowledge-based economy. To succeed in this new information-based and highly technological society, students need to develop their capabilities in STEM to levels much beyond what was considered acceptable in the past.” by National Science Foundation.</p>
                                <div className="countdown-one__list list-unstyled">

                                    {/* <Countdown date={Date.now() + 5000000000} /> */}

                                </div>
                            </div>
                        </div>
                        <div className="col-lg-6">
                            <div className="become-teacher__form">
                                <div className="become-teacher__form-top">
                                    <h2 className="become-teacher__form-title">
                                        Register for upcoming courses
                                </h2>
                                </div>
                                <form id = "userform" className="become-teacher__form-content contact-form-validated" onSubmitCapture={(e) => {
                                e.preventDefault();
                                const name = event.target.name.value;
                                const email = event.target.email.value;
                                const phone_no = event.target.phone.value;
                                const comment = event.target.message.value;
                                createUserForm(
                                    { variables: { name, email, phone_no, comment } }
                                );
                        
                            }} onSubmit =  {handleButtonClick}>
                                <input type="text" placeholder="Your Name" name="name" required />
                                <input type="text" placeholder="Email Address" name="email" required />
                                <input type="text" placeholder="Phone Number" name="phone" />
                                <input type="text" placeholder="Comment" name="message" />

                                <button type="submit" className="thm-btn become-teacher__form-btn">Register</button>
                            </form>
                            <div id="snackbar">Thanks For Registering</div>

                                {/* {mutationLoading && <p>Loading...</p>}
                                {mutationError && <p>Error :( Please try again</p>} */}
                                <div className="result text-center"></div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        );
    }


const ADD_DATA = gql`
mutation PostMutation($name: String!, $email: String!, $phone_no: String!, $comment: String!) {
    createUserForm( 
     input: {
          data: {
            name: $name
            email: $email
            phone_no: $phone_no
            comment: $comment
          }
      }
    )
    {
       userForm{
        id
        name
        email
        phone_no
        comment
      }
    }
  }`;
export default CountdownKipso;
