import React, { Component } from 'react';
import { useQuery } from '@apollo/react-hooks';
import gql from "graphql-tag";

import SliderOne from "./SliderOne";
import SliderPage from "./SliderPage";

import CallToActionThree from "./CallToActionThree";
import VideoTwo from "./VideoTwo";
import CallToActionSeven from "./CallToActionSeven";
import Courses from "./Courses";

const MainPage = () => {
    const Testimony = gql`
    query{
        
        qtTestimonial{
            Intro{
                VideoUrl
                number
            }
            Student{
            VideoUrl
            number
            }
            Parent{
                VideoUrl
                number
            }
            Instructor{
                VideoUrl
                number
            }
        }  
    }
    `;
 
    const { data, loading, error } = useQuery(Testimony);
    
    if (loading) return <p>Loading...</p>;
    if (error) return <p>Error</p>;

    const introData = data.qtTestimonial.Intro;
    const testimony_Student = data.qtTestimonial.Student;
    const testimony_Parent = data.qtTestimonial.Parent;
    const testimony_Instructor = data.qtTestimonial.Instructor;
    // const courses = data.courses;

    if (testimony_Instructor && testimony_Student && testimony_Parent && testimony_Instructor )
        return (
            <> 
                <SliderPage />
                <SliderOne data={introData} />
                {/* <Courses data={courses}/>   */}
                <Courses/>
                <CallToActionThree data={testimony_Student} />
                <VideoTwo data={testimony_Parent} />
                <CallToActionSeven data={testimony_Instructor} />

            </>

        )
}


export default MainPage;

