import React from 'react';
import Swiper from 'react-id-swiper';
import 'swiper/css/swiper.css';
import { useQuery } from '@apollo/react-hooks';
import gql from "graphql-tag";
import CourseCat from "./CourseCat"

const CourseCatOne = () => {
    const { data, loading, error } = useQuery(category);
    // console.log(data)
    if (loading) return <p>Loading...</p>;
    if (error) return <p>Error</p>;

    var elements = [];
    for (var i = 0; i < data.courseCategories.length; i++) {
        // console.log(JSON.stringify(data.courseCategories[i]));
        elements.push(<CourseCat category={data.courseCategories[i]} />);
    }
    const params = {
        slidesPerView: 3,
        loop: false,
        speed: 1000,
        spaceBetween: 30,
        autoplay: {
            delay: 3000,
            disableOnInteraction: false
        },

        // Responsive breakpoints
        breakpoints: {
            1024: {
                slidesPerView: 3
            },
            768: {
                slidesPerView: 2
            },
            640: {
                slidesPerView: 2

            },
            320: {
                slidesPerView: 1
            }
        }
    }
    // category-gap
    return (
        <section className="thm-gray-bg course-category-one">
            {/* <div className="container-fluid text-center"> */}
            <div className="block-title text-center">
                <h2 className="block-title__title">Browse online <br />
                        course categories</h2>
            </div>
            {/* className="course-category-one__carousel" */}
            <div  >
                <Swiper {...params} >

                    {elements}


                </Swiper>

            </div>
        </section>
    );
}

const category = gql`
{
    courseCategories{
      Name
      ImageUrl  
    }
  }`
export default CourseCatOne;
