var player2 = new Array();
var playing2 = false;

class CallToActionThree extends Component {
    
    constructor(props) {
        super(props)
        this.state = {
            testimony: this.props.data
        }
    }

    onYouTubeIframeAPIReady() {

        const { testimony } = this.state;
        testimony.map((t) => {
            player2[t.number] = new YT.Player('video2' + t.number, {
                events: {
                    'onStateChange': this.onPlayerStateChange,
                }
            });

        })

    };

    onPlayerStateChange = (event) => {
        if (event.data == YT.PlayerState.PLAYING) {
            // console.log('video started');
            $('#carousel-example-1z').carousel('pause');
            playing2 = true;
            $(".controlsStudents").hide()
        }

        else if (event.data == YT.PlayerState.PAUSED) {
            // console.log('video paused');
            $('#carousel-example-1z').carousel('cycle');
            playing2 = false;
            $(".controlsStudents").show()

        }

        return 1;
    };

    componentDidMount = () =>{
        $('#carousel-example-1z').carousel('cycle');
        //this.onYouTubeIframeAPIReady()

    } 

    render(){
        const {testimony} = this.state;
        return (
            <section className="cta-three">
                <div className="container">
                    <div className="row">
                        <div className="col-lg-7 clearfix">
                            <div id="carousel-example-1z" className="carousel slide carousel-fade" data-interval="4600" data-pause="hover" data-ride="carousel"
                            >
                                <ol className="carousel-indicators">
                                    {
                                        testimony.map((t, index) => {
                                            return (
                                                <li key={index.toString()} data-target="#carousel-example-1z" data-slide-to={t.number} className={t.number == '1' ? 'active' : null}></li>

                                            )
                                        })
                                    }

                                </ol>
                                <div className="carousel-inner" role="listbox">

                                    {
                                        testimony.map((t, index) => {
                                            return (
                                                <div key={index.toString()} className={t.number == '1' ? 'carousel-item active' : 'carousel-item'} >
                                                    <div className="col-xl-5 carousel-youtube_video">
                                                        {/*<iframe  loading="lazy" id = {'video2' + t.number}  src={t.VideoUrl + '?enablejsapi=1'} frameBorder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowFullScreen=""></iframe>*/}
                                                        <iframe id={`video2` + t.number} data-src={t.VideoUrl} className="lazyload" frameBorder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope;" allowFullScreen></iframe>
                                                    </div>
                                                </div>
                                            )
                                        })
                                    }

                                </div>
                                <div className= "controlsStudents">
                                    <a className="carousel-control-prev" href="#carousel-example-1z" role="button" data-slide="prev">
                                        <span className="carousel-control-prev-icon" aria-hidden="true"></span>
                                        <span className="sr-only">Previous</span>
                                    </a>
                                    <a className="carousel-control-next" href="#carousel-example-1z" role="button" data-slide="next">
                                        <span className="carousel-control-next-icon" aria-hidden="true"></span>
                                        <span className="sr-only">Next</span>
                                    </a>
                                </div>
                            </div>

                        </div>

                        <div className="col-lg-5">
                            <div className="cta-three__content">
                                <div className="block-title text-left">
                                    <h2 className="block-title__title">Why Choose QtLearn</h2>
                                </div>
                                <p className="cta-three__text">We offer a world class STEAM education for 21st century learners. The courses are designed and developed by technology experts and curated by educationalists. We provide a very unique methodology of teaching K-12 students with the DESI (Design, Electronics, Software and Integration) model.</p>
                                <div className="cta-three__single-wrap">
                                    <div className="cta-three__single">
                                        <i className="kipso-icon-strategy"></i>
                                        <p className="cta-three__single-text">Professional
                                                Courses</p>
                                    </div>
                                    <div className="cta-three__single">
                                        <i className="kipso-icon-training"></i>
                                        <p className="cta-three__single-text">Live
                                                Learning</p>
                                    </div>

                                    <div className="cta-three__single">
                                        <i className="kipso-icon-human-resources"></i>
                                        <p className="cta-three__single-text">Expert
                                                Teachers</p>
                                    </div>
                                </div>
                                {/* <a href="#" className="thm-btn">Learn More</a> */}
                            </div>
                        </div>
                    </div>
                </div>
            </section >
        );
    }

}

export default CallToActionThree;
